
$main-head-font-size-web: 42px;
//  div{
//     border: 1px solid red;
// }

.pickeddiv{
    display: flex;
    width: 100%;
    margin: auto;
    margin-top: 60px;
}
.storesflex{
    width: 45%;
}
.imageflex{
    width: 48%;
    margin: auto;
    padding-bottom: 0px;
}
.shippedtext{
    font-family: Gilroy;
    width: 415px;
    padding-top: 50px;

}
.playstore{
    height: 30px;
}
.appstore{
    height: 30px;
    margin-left: 20px;
}
.iconsdivv{
    width: fit-content;
    margin: auto;
    // margin-top: 20px;
}
.inputnumber{
    width: 200px;
    background-color: #F5F9FF;
    border: 1px solid rgba(9, 28, 63, 0.2);
    border-radius: 28px;
    padding: 8px;

}

.icons{
    display: flex;
    width: fit-content;
    /* margin: auto; */
    margin-top: 25px;
}
.applogo{
    height: 30px;
}
.playlogo{
    height: 30px;
    padding-left: 20px;
}
.numberflex{
    display: flex;
    margin: auto;
}
.flagflexx{
    width: 45px;
}
.flagimg{
    height: 15px;
    padding-left: 10px;
    margin-bottom: 2px;
}
.secondflex{
    width: 65%;
}
.digit{
    font-family: Roboto;
    border: none;
    outline: none;
    font-size: 14px;
    color: rgba(9, 28, 63, 0.6);
    // width: 200px;
    margin-left: 3px;
    background-color: #F5F9FF;
}
.arrowflex{
    width: 20%;
    cursor: pointer;
}
.rightarrow{
    width: 25px;
}
.fulldiv{
    width: 270px;
    /* margin: auto; */
    border: 1px solid rgba(9, 28, 63, 0.2);
    border-radius: 20px;
    padding: 6px;
    background-color: #F5F9FF;
    margin-top: 30px;
}
.imgbtn{
    border: none;
    outline: none;
    background-color: #F5F9FF;
    text-align: center;
    position: relative;
    top: -1px;
    padding-bottom: 5px 0px 5px 0px;
}
.linediv-clss{
    display: inline-block;
    border: 1px solid gray;
    width: 1px;
    height: 18px;
    /* background-color: #091C3F; */
    margin-left: -2px;
    margin-top: 4px;
    border-radius: 3px;

}
.onimg{
    height: 350px;
    margin-bottom: 19px;
}

    @media(max-width: 720px){
        .pickeddiv{
            flex-direction: column;
            margin-bottom: 80px;
        }
        .storesflex{
            width: 100%;
        }
        .imageflex{
            display: none;
        }
        .shippedtext{
            width: 200px;
            margin: auto;
            padding-top: 10px;
            text-align: center;
        }

        .icons{
            margin: auto;
            margin-top: 20px;
        }
        .applogo{
            height: 28px;
        }
        .playlogo{
            height: 28px;
            padding-left: 10px;
        }
        .pickeddiv{
            margin-top: 20px;
        }
        .fulldiv{
            margin: auto;
            margin-top: 30px;

        }
    }
